@import "_normalize";
@import "_body";
@import "_variables";
@import "_mixins";
@import "_header";
@import "_hero";
@import "_footer";
@import "_reason";
@import "_services";
@import "_customers";
@import "_news";
@import "_service-description";
@import "_option";

.wave-blue {
  width: 100%;
  position: absolute;
  z-index: -2;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  //background-color: linear-gradient(90deg, #94D6E8 0%, #4CBAD8 100%);
  mix-blend-mode: normal;
  //opacity: 0.5;
  transform: matrix(1, 0, 0, 1, 0, 0);

  &__image {
    margin: 0;
    padding: 0;
    width: 100%;
  }
}

.wave-center {
  position: absolute;
  margin: 0;
  //left: calc(100vw - 676px);
  top: 2706px;
  right: 0;
  z-index: -1;

  &__image {
    margin: 0;
    padding: 0;
  }
}
.wave-footer {
  position: absolute;
  margin: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;

  &__image {
    width: 100%;

  }
}

.btn {
  width: 40px;
  height: 40px;
  background: $blue;
  border: none;
  border-radius: 50%;
  box-shadow: 0 4px 14px rgba(76, 186, 216, 0.5);

  &:hover {
    cursor: pointer;
    transform: scale(0.9);
  }
}
